.project-card {
  padding: 18px;
  margin: 20px;
}

.project-details {
  padding: 10px;
  margin: 15px;
}

.dashboard {
  padding: 10px;
  margin: 20px;
}

.Root {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.Grow {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-decoration: none;
}

.chatbox {
  padding: 0px;
  background-color: #212121;
}
.box {
  padding: 15px;
  overflow: scroll;
  height: 522px;
}
.bottom-box {
  height: 70px;
  padding: 8px;
}
.chat-message {
  padding: 8px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 8px;
}

#question {
  background-color: #0094da;
  color: white;
  padding: 10px;
  text-align: center;
}

#question h4 {
  background-color: #fff;
  color: #0b0e21;
  display: inline-block;
  padding: 10px 25px;
  font-size: 20px;
  font-weight: 600;
}

#question p {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 2px;
  padding-left: 50px;
  padding-right: 50px;
}

#answers ul {
  list-style-type: none;
  padding: 0;
  margin-top: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

#answers li {
  background-color: #fff;
  border: 2px solid #0094da;
  min-height: 70px;
  width: 48%;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 30px;
  -webkit-transition: color 0.3s ease, border-color 0.3s ease, -webkit-transform 0.3s ease;
  transition: color 0.3s ease, border-color 0.3s ease, -webkit-transform 0.3s ease;
  transition: color 0.3s ease, border-color 0.3s ease, transform 0.3s ease;
  transition: color 0.3s ease, border-color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
  cursor: pointer;
}

#answers li span {
  background-color: #0084da;
  color: #fff;
  font-size: 30px;
  -webkit-flex: 75px 1;
          flex: 75px 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-transition: background-color 0.3s ease;
  transition: background-color 0.3s ease;
}

#answers li p {
  color: #0b0e21;
  font-size: 16px;
  font-weight: 600;
  -webkit-flex: calc(100% - 75px) 1;
          flex: calc(100% - 75px) 1;
  margin: auto 20px;
  -webkit-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

#answers li:after {
  display: block;
}

#answers li:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

#answers li.right {
  border-color: #1ea55b;
  color: #1ea55b;
}

#answers li.right span {
  background-color: #1ea55b;
}

#answers li.right p {
  color: #1ea55b;
}

#answers li.wrong {
  border-color: #dc0a0a;
  color: #dc0a0a;
}

#answers li.wrong span {
  background-color: #dc0a0a;
}

#answers li.wrong p {
  color: #dc0a0a;
}

#submit {
  text-align: center;
  margin: 20px 0;
}

.fancy-btn {
  border: 2px solid #0b0e21;
  border-radius: 0;
  background-color: #0b0e21;
  color: #fff;
  display: inline-block;
  font-size: 18px;
  font-weight: 600;
  padding: 14px 75px;
  margin: 0 auto;
  text-transform: uppercase;
  -webkit-transition: color 0.2s ease, background-color 0.2s ease;
  transition: color 0.2s ease, background-color 0.2s ease;
}

.fancy-btn:hover {
  background-color: #fff;
  color: #0b0e21;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.popup {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
}

.popup h1 {
  background-color: #0094da;
  color: white;
  border-bottom: 1px solid #ccc;
  padding: 20px;
  margin-top: 0;
}

.popup p {
  font-size: 18px;
  letter-spacing: 1px;
  margin: 20px 10% 0;
}

.popup .fancy-btn {
  margin: 20px auto;
}

footer {
  background-color: #0b0e21;
  color: white;
  letter-spacing: 1px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  text-align: center;
}

footer p {
  margin: 0;
}

footer small {
  margin-bottom: 10px;
}

footer span {
  color: crimson;
}

@media (max-width: 768px) {
  #question p {
    padding-left: 10px;
    padding-right: 10px;
  }

  #answers li {
    width: 100%;
  }

  #submit button {
    width: 100%;
  }
}
@media (max-width: 480px) {
  footer {
    padding: 5px;
  }

  footer small {
    display: none;
  }
}

